@keyframes emerge {
  from { transform: scale(0); }
  to { transform: scale(1); }
}
@keyframes highlight {
  from {
    box-shadow:
      0 0 80px var(--fg),
      0 0 60px var(--fg),
      0 0 60px var(--fg),
      0 0 40px var(--fg),
      0 0 40px var(--fg),
      0 0 20px var(--fg),
      0 0 20px var(--fg);
  }
  to { box-shadow: none; }
}

.dot {
  position: absolute;
  background: var(--fg);
  width: 10px;
  height: 10px;
  border-radius: 100vmax;
  animation: .05s emerge, 1s highlight;
}
